<template>
  <div class="ihris-section pb-4">
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn :disabled="!canSave" class="success darken-1" dark @click="$refs.form.validate()">
            <v-icon light>mdi-content-save</v-icon>
            <span>Save</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-card
      class="mx-auto"
      max-width="700"
      outlined
    >
      <v-card-title class=" justify-center darken-1 primary white--text text-uppercase font-weight-bold">Post Interview Information</v-card-title>
      <v-card-text class="my-3">
        <v-textarea
          :error-messages="errors.motivation" 
          @change="errors.motivation = []"
          label="Motivation"
          v-model="motivation" 
          outlined 
          hide-details="auto" 
          :rules="motivationRules"
          dense
        >
          <template #label>Motivation<span class="red--text font-weight-bold">*</span></template>
        </v-textarea>
        <v-card>
          <v-card-title primary-title>
            Interview Assessment Types<span class="red--text font-weight-bold">*</span>
          </v-card-title>
          <v-card-text>
            <v-checkbox
              v-for="(assessment, index) in assessments"
              :key="index"
              v-model="selectedAssessments"
              :label="assessment.label"
              :value="assessment.value"
              dense
            ></v-checkbox>
          </v-card-text>
        </v-card>
        <br v-if="selectedAssessments.includes('oral')">
        <v-text-field
          v-if="selectedAssessments.includes('oral')"
          :error-messages="errors.oral" 
          @change="errors.oral = []"
          label="Oral Score"
          v-model="oral" 
          outlined 
          hide-details="auto" 
          :rules="oralRules"
          dense
        >
          <template #label>Oral Score<span class="red--text font-weight-bold">*</span></template>
        </v-text-field>
        <br v-if="selectedAssessments.includes('written')">
        <v-text-field
          v-if="selectedAssessments.includes('written')"
          :error-messages="errors.written" 
          @change="errors.written = []"
          label="Written Score"
          v-model="written" 
          outlined 
          hide-details="auto" 
          :rules="writtenRules"
          dense
        >
          <template #label>Written Score<span class="red--text font-weight-bold">*</span></template>
        </v-text-field>
        <br v-if="selectedAssessments.includes('practical')">
        <v-text-field
          v-if="selectedAssessments.includes('practical')"
          :error-messages="errors.practical" 
          @change="errors.written = []"
          label="Practical Score"
          v-model="practical" 
          outlined 
          hide-details="auto" 
          :rules="practicalRules"
          dense
        >
          <template #label>Practical Score<span class="red--text font-weight-bold">*</span></template>
        </v-text-field>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      motivation: '',
      errors: {
        motivation: [],
        oral: [],
        written: [],
        practical: []
      },
      assessments: [{
        label: "Oral Score",
        value: "oral"
      }, {
        label: "Written Score",
        value: "written"
      }, {
        label: "Practical Score",
        value: "practical"
      }],
      selectedAssessments: [],
      oral: '',
      written: '',
      practical: ''
    }
  },
  methods: {
    save() {
      // let resource = {
      //   resourceType: "Basic",
      //   meta: {
      //     profile: ["http://ihris.org/fhir/StructureDefinition/post-interview-profile"]
      //   },
      //   extension: [{
      //     url: "http://ihris.org/fhir/StructureDefinition/post-interview",
      //     extension: []
      //   }]
      // }
      // for(let assessment of assessments) {
      //   resource.extension[0].extension.push({
      //     url: "assessment",
      //     valueCoding: {
      //       system: "http://ihris.org/fhir/CodeSystem/interview-assessment-codesystem",
      //       code: assessment
      //     }
      //   })
      // }
    }
  },
  computed: {
    canSave () {
      if(!this.selectedAssessments.length || !this.motivation || (this.selectedAssessments.includes('oral') && !this.oral) || (this.selectedAssessments.includes('written') && !this.written) || (this.selectedAssessments.includes('practical') && !this.practical)) {
        return false
      }
      return true
    },
    motivationRules: function() {
      if ( !this.motivation ) {
        return [ v => !!v || "Motivation is required" ]
      } else {
        return []
      }
    },
    oralRules: function() {
      const num_check = v => {
        //let num = Number(v)
        return (!isNaN(v) && /^-?\d+(\.\d+)?$/.test(v)) ||"Oral score must be a Decimal"
      }
      let rules = [ num_check ]
      if ( !this.oral ) {
        rules.push ( v => !!v || "Oral score is required" )
      }
      if(this.oral > 100) {
        rules.push("Oral score must not exceeds 100")
      }
      return rules
    },
    writtenRules: function() {
      const num_check = v => {
        return (!isNaN(v) && /^-?\d+(\.\d+)?$/.test(v)) ||"Written score must be a Decimal"
      }
      let rules = [ num_check ]
      if ( !this.written ) {
        rules.push ( v => !!v || "Written score is required" )
      }
      if(this.written > 100) {
        rules.push("Written score must not exceeds 100")
      }
      return rules
    },
    practicalRules: function() {
      const num_check = v => {
        //let num = Number(v)
        return (!isNaN(v) && /^-?\d+(\.\d+)?$/.test(v)) ||"Practical score must be a Decimal"
      }
      let rules = [ num_check ]
      if ( !this.practical ) {
        rules.push ( v => !!v || "Practical score is required" )
      }
      if(this.practical > 100) {
        rules.push('Practical score must not exceeds 100')
      }
      return rules
    }
  }
}
</script>